body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.hoverLarge {
  transition: transform .2s;
}

.hoverLarge:hover {
  transform: scale(1.1);
}

.hoverLarge12 {
  transition: transform .2s;
}

.hoverLarge12:hover {
  transform: scale(1.2);
}

.highlightColor {
  color: #6FC84F;
}

.highlightColorLight {
  color: #95d77e
}

.footer__link-item, *:any-link {
  color: #6FC84F;
}

.margin--auto {
  margin: auto;
}

html {
  scroll-behavior: smooth;
}

.mobile-center-full-left {
  text-align: left;
}

@media only screen and (max-width: 800px) {
  .mobile-center-full-left {
    text-align: center;
  }
}
.card {
  border: none
}
.aspect-ratio-box-16-9 {
  height: 0;
  overflow: hidden;
  padding-top: 56.25%;
  position: relative;
}

.aspect-ratio-box-inside-16-9 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}